import Vue from "vue";
import Router from "vue-router";
import HelloWorld from "../components/HelloWorld";
import ThePRP from "../components/ThePRP";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "HelloWorld",
      component: HelloWorld
    },
    {
      path: "/theprp",
      name: "The PRP",
      component: ThePRP
    }
  ]
});
